<template>
  <div class="zd-common-wp100">
    <Row type="flex" justify="space-around" class="zd-common-white-b zd-common-tc">
      <Col class="zd-common-ptb30">
        <p>物品类别</p>
        <p class="zd-common-fs30 zd-common-mt10">
          <v-count-up :start-value="0" :end-value="allDetail.goodsKindNum"></v-count-up>
        </p>
      </Col>
      <Col class="zd-common-ptb30">
        <p>物品档案</p>
        <p class="zd-common-fs30 zd-common-mt10">
          <v-count-up :start-value="0" :end-value="allDetail.goodsInfoNum"></v-count-up>
        </p>
      </Col>
      <Col class="zd-common-ptb30">
        <p>物品总量</p>
        <p class="zd-common-fs30 zd-common-mt10">
          <v-count-up :start-value="0" :end-value="allDetail.goodsInfoTotal"></v-count-up>
        </p>
      </Col>
      <Col class="zd-common-ptb30">
        <p>物品总金额</p>
        <p class="zd-common-fs30 zd-common-mt10">
          ￥<v-count-up :start-value="0" :end-value="allDetail.goodsInfoTotalPrice"></v-count-up>
        </p>
      </Col>
      <Col class="zd-common-ptb30">
        <p>预警物品数量</p>
        <p class="zd-common-fs30 zd-common-mt10">
          <v-count-up :start-value="0" :end-value="allDetail.warningTotal"></v-count-up>
        </p>
      </Col>
    </Row>
    <div class="zd-common-clearFix zd-common-mt20">
      <div class="zd-common-fl zd-common-wp50">
        <div class="zd-common-white-b zd-common-mr10">
          <h3 slot="header" class="zd-common-modal-header zd-common-border-bottom1">
            <span>入库物品Top10</span>
            <Icon @click="toRouter('inDetails')" type="ios-arrow-dropright-circle" class="zd-common-fr zd-common-des-font-c zd-common-mt17 zd-common-fs20 zd-common-cp" />
          </h3>
          <div class="zd-common-clearFix">
            <ul class="zd-common-clearFix zd-common-fr zd-common-mr20 zd-common-mt10">
              <li @click="changeIn('0')" :class="[inKind==='0'?'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-cm-home-active zd-common-cp':'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-common-cp']" style="border-right: 0;">本周</li>
              <li @click="changeIn('1')" :class="[inKind==='1'?'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-cm-home-active zd-common-cp':'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-common-cp']" style="border-right: 0;">本月</li>
              <li @click="changeIn('2')" :class="[inKind==='2'?'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-cm-home-active zd-common-cp':'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-common-cp']">本年</li>
            </ul>
          </div>
          <div class="zd-common-h350">
            <chart :options="inStatistics" ref="inStatistics" auto-resize style="width: 100%; height: 300px;"></chart>
          </div>
        </div>
      </div>
      <div class="zd-common-fl zd-common-wp50">
        <div class="zd-common-white-b zd-common-ml10">
          <h3 slot="header" class="zd-common-modal-header zd-common-border-bottom1">
            <span>出库物品Top10</span>
            <Icon @click="toRouter('outDetails')" type="ios-arrow-dropright-circle" class="zd-common-fr zd-common-des-font-c zd-common-mt17 zd-common-fs20 zd-common-cp" />
          </h3>
          <div class="zd-common-clearFix">
            <ul class="zd-common-clearFix zd-common-fr zd-common-mr20 zd-common-mt10">
              <li @click="changeOut('0')" :class="[outKind==='0'?'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-cm-home-active zd-common-cp':'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-common-cp']" style="border-right: 0;">本周</li>
              <li @click="changeOut('1')" :class="[outKind==='1'?'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-cm-home-active zd-common-cp':'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-common-cp']" style="border-right: 0;">本月</li>
              <li @click="changeOut('2')" :class="[outKind==='2'?'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-cm-home-active zd-common-cp':'zd-common-fl zd-common-pl10 zd-common-pr10 zd-common-border zd-common-cp']">本年</li>
            </ul>
          </div>
          <div class="zd-common-h350">
            <chart :options="outStatistics" ref="outStatistics" auto-resize style="width: 100%; height: 300px;"></chart>
          </div>
        </div>
      </div>
    </div>
    <div class="zd-common-white-b zd-common-mt20">
      <h3 slot="header" class="zd-common-modal-header zd-common-border-bottom1">
        <span>快捷操作入口</span>
      </h3>
      <Row type="flex" justify="space-around" class="zd-common-tc">
        <Col class="zd-common-ptb30 zd-common-cp zd-cm-home-entry" @click.native="toRouter('putIn')">
          <Icon type="ios-home" class="zd-common-fs30 zd-common-des-font-c"/>
          <p class="zd-common-fs16 zd-common-mt10">入库</p>
        </Col>
        <Col class="zd-common-ptb30 zd-common-cp zd-cm-home-entry" @click.native="toRouter('out')">
          <Icon type="md-train" class="zd-common-fs30 zd-common-des-font-c"/>
          <p class="zd-common-fs16 zd-common-mt10">出库</p>
        </Col>
        <Col class="zd-common-ptb30 zd-common-cp zd-cm-home-entry" @click.native="toRouter('goodsProvide')">
          <Badge :count="provideNum">
            <Icon type="logo-xbox" class="zd-common-fs30 zd-common-des-font-c"/>
            <p class="zd-common-fs16 zd-common-mt10">物品发放</p>
          </Badge>
        </Col>
        <Col class="zd-common-ptb30 zd-common-cp zd-cm-home-entry" @click.native="toRouter('allocation')">
          <Icon type="ios-repeat" class="zd-common-fs30 zd-common-des-font-c"/>
          <p class="zd-common-fs16 zd-common-mt10">调拨</p>
        </Col>
        <Col class="zd-common-ptb30 zd-common-cp zd-cm-home-entry" @click.native="toRouter('goodsStatistics')">
          <Icon type="md-pulse" class="zd-common-fs30 zd-common-des-font-c"/>
          <p class="zd-common-fs16 zd-common-mt10">物品统计</p>
        </Col>
      </Row>
    </div>
  </div>
</template>
<script>
  import VCountUp from 'vue-countupjs'
  export default {
    components:{
      VCountUp
    },
    data() {
      return {
        //入库统计
        inStatistics:{
          title:{
            text:'',
            x:'center'
          },
          color:["#32CD32","#6495ED","#FF7F50","#87CEFA","#DA70D6","#6495ED"],
          tooltip : {
            trigger: 'item'
          },
          legend: {
            data: [],
            bottom: 10,
            left: 'center',
            itemWidth:30,
          },
          grid: {
            left: '3%',
            right: '4%',
            top: '4%',
            bottom: '4%',
            containLabel: true
          },
          xAxis : [
            {
              type : 'category',
              data : [],
              axisTick: {
                alignWithLabel: true
              },
              axisLabel:{
                rotate:30
              }
            }
          ],
          yAxis:[
            {
              type : 'value'
            }
          ],
          series:[
            {
              name:'数量',
              type:'bar',
              data:[]
            },
          ]
        },
        //出库统计
        outStatistics:{
          title:{
            text:'',
            x:'center'
          },
          color:["#32CD32","#6495ED","#32CD32","#FF7F50","#87CEFA","#DA70D6","#6495ED"],
          tooltip : {
            trigger: 'item'
          },
          legend: {
            data: [],
            bottom: 10,
            left: 'center',
            itemWidth:30,
          },
          grid: {
            left: '3%',
            right: '4%',
            top: '4%',
            bottom: '4%',
            containLabel: true
          },
          xAxis : [
            {
              type : 'category',
              data : [],
              axisTick: {
                alignWithLabel: true
              },
              axisLabel:{
                rotate:30
              }
            }
          ],
          yAxis:[
            {
              type : 'value'
            }
          ],
          series:[
            {
              name:'数量',
              type:'bar',
              data:[]
            },
          ]
        },
        allDetail:{},
        outKind:'0',
        inKind:'0',
        showProvideNum:0
      }
    },
    computed:{
      provideNum(){
          return this.$store.state.provideNum;
      }
    },
    created(){
      this.indexDetail();
      this.intoWarehouseTop10(this.inKind);
      this.outWarehouseTop10(this.outKind);
      this.$store.dispatch('getProvideNum');
    },
    methods:{
      //跳转
      toRouter(path){
        this.$router.push({path:'/'+path});
      },
      //切换周、月、年
      changeOut(kind){
        this.outKind = kind;
        this.outWarehouseTop10(kind);
      },
      changeIn(kind){
        this.intoWarehouseTop10(kind);
        this.inKind = kind;
      },
      //数据概览
      indexDetail(){
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!indexDetail.action?operationCode=sr_storeroom_homepage',
          data:{},
        })
        .then((response) => {
          this.allDetail = response.data;
        })
        .catch((error) => {
          this.postErrorTip('请求失败');
          console.log(error);
        });
      },
      //入库top10 //0--本周 1---本月 2---本年
      intoWarehouseTop10(kind){
        this.inStatistics.xAxis[0].data = [];
        this.inStatistics.series[0].data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!intoWarehouseTop10.action?operationCode=sr_storeroom_homepage',
          data:{
            kind:kind
          },
        })
          .then((response) => {
            let aData = response.data;
            aData.map((item)=>{
              this.inStatistics.xAxis[0].data.push(item.name);
              this.inStatistics.series[0].data.push(item.num);
            });
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //入库top10 //0--本周 1---本月 2---本年
      outWarehouseTop10(kind){
        this.outStatistics.xAxis[0].data = [];
        this.outStatistics.series[0].data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!outWarehouseTop10.action?operationCode=sr_storeroom_homepage',
          data:{
            kind:kind
          }
        })
        .then((response) => {
          let aData = response.data;
          aData.map((item)=>{
            this.outStatistics.xAxis[0].data.push(item.name);
            this.outStatistics.series[0].data.push(item.num);
          });
        })
        .catch((error) => {
          this.postErrorTip('请求失败');
          console.log(error);
        });
      },
    },
    mounted(){}
  }
</script>
<style lang="less" scoped>
  .zd-cm-home-entry,.zd-common-des-font-c{
    transition: color .5s ease-out;
  }
  .zd-cm-home-entry:hover{
    color: #1c90f3;
  }
  .zd-cm-home-entry:hover .zd-common-des-font-c{
    color: #1c90f3;
  }
</style>



